import React,{useState}from "react";
import laptop from '../assets/Home/lapko.jpg'
import iphone from '../assets/Home/iphone_3.jpg'
import hea from '../assets/Home/hea1.jpg'
import Product from "./Product";
import './Home.css'
const Home = (props) => {
    console.log(props.name);
    return (
        <div>
            <div id="carouselExampleIndicators" className="carousel slide" data-bs-ride="true">
                <div className="carousel-indicators">
                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button>
                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1" aria-label="Slide 2"></button>
                    <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2" aria-label="Slide 3"></button>
                </div>
                <div className="container py-3">
                    <div className="row">
                        <div className="text-left">
                            <h1 style={{ color: 'green', fontSize: '30px', alignContent: 'baseline' }}>{props.name ? `Welcome  ${props.name}` : ""}</h1>
                        </div>
                    </div>

                </div>
                <div className="carousel-inner" >
                    <div className="carousel-item active">
                        <img src={hea} className="d-block  w-100" alt="Laptop" height="505px" />
                    </div>
                    <div className="carousel-item">
                        <img src={iphone} className="d-block w-100" alt="Headphone" height="505px" />
                    </div>
                    <div className="carousel-item">
                        <img src={laptop} className="d-block w-100" alt="Laptop" height="505px" />
                    </div>
                </div>
                <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Previous</span>
                </button>
                <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="visually-hidden">Next</span>
                </button>
            </div>
            {/* <div class="scrollmenu">
                <a href="#home"> Phone</a>
                <a href="#news">Laptop</a>
                <a href="#contact">Headphone</a>
                <a href="#about">Aceesairos</a>
            </div> */}
            <Product />
        </div>
        

    );
}


export default Home;