import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import Thank from './Thank';
import { NavLink, Link, Redirect } from 'react-router-dom';
import { useState } from 'react';
import { collection, addDoc, doc, getDocs, updateDoc, deleteDoc } from "firebase/firestore";
import { db, storage } from "../Firebase";


const Checkout = () => {


    const [erroMsg, setError] = useState("");
    const [count,setcount] = useState(0);
    const [submitbuttondisable,setSubmitbuttondisable] = useState(false);

    const [values, setValues] = useState({
        Firstname: "",
        Address: "",
        PhoneNumber: "",
        email:"",
    });

    const handleSubmit = () => {
        if (!values.Firstname || !values.Address || !values.PhoneNumber|| !values.email) {
            setError("Fill all fields");
        } else {
            setError("");
            setcount(1);
            console.log("Pressed");
            setValues({
                Firstname: "",
                Address: "",
                PhoneNumber: "",
            });
        }

    }

    const state = useSelector((state) => state.addItem)
    var total = 0;
    const itemList = (item) => {
        total = total + item.price;
        console.log(total);
        return (
            <li className="list-group-item d-flex justify-content-between lh-sm">
                <div>
                    <h6 className="my-0">{item.title}</h6>
                </div>
                <span className="text-muted">{item.price}</span>
            </li>
        );
    }

    return (
        <>
            <div className="container my-5">
                <div className="row g-5">
                    <div className="col-md-5 col-lg-4 order-md-last">
                        <h4 className="d-flex justify-content-between align-items-center mb-3">
                            <span className="text-primary">Your cart</span>
                            <span className="badge bg-primary rounded-pill">{state.length}</span>
                        </h4>
                        <ul className="list-group mb-3">
                            {state.map(itemList)}

                            <li className="list-group-item d-flex justify-content-between">
                                <span>Total (Rupee)</span>
                                <strong>{total}</strong>
                            </li>
                        </ul>
                    </div>
                    <div className="col-md-7 col-lg-8">
                        <h4 className="mb-3">Billing address</h4>
                        <div className="row g-3">
                            <div className="col-sm-6">
                                <label htmlFor="firstName" className="form-label">Full Name:</label>
                                <input type="text" className="form-control" onChange={(event) =>
                                    setValues((prev) => ({
                                        ...prev,
                                        Firstname: event.target.value,
                                    }))
                                } />
                            </div>
                            <div className="col-sm-6">
                                <label htmlFor="firstName" className="form-label">Phone Number</label>
                                <input type="text" className="form-control" onChange={(event) =>
                                    setValues((prev) => ({
                                        ...prev,
                                        PhoneNumber: event.target.value,
                                    }))} />

                            </div>

                            <div className="col-12">
                                <label htmlFor="username" className="form-label">Username</label>
                                <div className="input-group has-validation">
                                    <span className="input-group-text">@</span>
                                    <input type="text" className="form-control" placeholder="Username" required="" />
                                </div>
                            </div>

                            <div className="col-12">
                                <label htmlFor="email" className="form-label">Email</label>
                                <input type="email" className="form-control" onChange={(event) =>
                                    setValues((prev) => ({
                                        ...prev,
                                        email: event.target.value,
                                    }))} placeholder="you@example.com" />

                            </div>

                            <div className="col-12">
                                <label htmlFor="address" className="form-label">Address</label>
                                <input type="text" className="form-control" onChange={(event) =>
                                    setValues((prev) => ({
                                        ...prev,
                                        Address: event.target.value,
                                    }))}  placeholder="Shipping Address" />
                            </div>

                        </div>
                        <div className="mb-3" style={{ color: "red", fontsize: 30 }}>
                            {erroMsg}
                        </div>
                        <hr className="my-2" />
                  
                        <button onClick={handleSubmit} disabled={submitbuttondisable}   className="btn btn-success  w-100">Order Place</button>
                        { count === 1 ? <Redirect to='/Thank'></Redirect> : ""}
                    </div>
                </div>
            </div>
        </>
    )
}

export default Checkout;
