import React from "react";
import parsal from '../assets/Home/parsal.jpg';

const Thank = () => {

  return (
    <>
      <div>
        <div className="container py-5 my-4">
          <div className="row">
            <div className="col-md-6">
              <h1 className="text-primary fw-bold mb-4">Thank You </h1>
              <h2 className="fw-bold mb-4" style={{ color: 'green' }}>Order Place with in 2 Days.....</h2>
            </div>
            <div className="col-md-6 d-flex justify-content-center">
              <img src={parsal} alt="About us " height="500px" width="600px"></img>
              {setTimeout(() => {
                window.location.href = '/home';
              }, 3000)}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Thank;